import React,{useState,useEffect} from 'react';
import MainPageLayout from '../components/MainPageLayout';
import {useShows} from '../misc/custom-hooks';
import {get_api} from '../misc/config';
import {ShowGrid} from '../components/shows/ShowGrid'
const Starred = () => {
  const [starred] = useShows();
  const [shows,setShow]=useState(null);  
  const [isLoading,setIsLoading]=useState(true);
  const [error,setError]=useState(null);

  useEffect(()=>{
    if(starred && starred.length >0)
    {
      const promises= starred.map(showId=>get_api(`shows/${showId}`))
      Promise.all(promises).then(result=>(result.map(show=>({show})))).then(results=>{
        setShow(results);
        setIsLoading(false);
      }).catch(err=>{setError(err.message);
      setIsLoading(false)});
      
    }
    else
    {
      setIsLoading(false);
    }
  },[starred])

  return (
      <MainPageLayout>{isLoading && <div>Page is still loading</div>}
      {error && <div>Error occured : {error}</div>}
      {!isLoading && !shows && <div>No shows were added</div>}
      {!isLoading && !error && shows && <ShowGrid data={shows} />}
      </MainPageLayout>
    )
}

export default Starred;
